}
.about-program__title {
  margin: 0;
  margin-bottom: 40px;
  font-size: 36px;
  line-height: 1;
  font-weight: 400;
}
.about-program__list {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  margin-bottom: 49px;
}
.about-program__item {
  position: relative;
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  min-height: 60px;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 20px;
  font-family: "Arial", sans-serif;
}
.about-program__item:last-of-type {
  margin-bottom: 0;
}
.about-program__small {
  display: none;
}
.about-program__icon {
  height: auto;
}
.about-program__icon-wrapper {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: hsl(97, 53%, 47%);
}
.about-program__icon--leaf {
  width: 26px;
}
.about-program__icon--tub {
  width: 30px;
}
.about-program__item--cutlery {
  width: 26px;
}
.about-program__icon--alarm {
  width: 25px;
}
.about-program__content {
  margin: 0;
}
@media (min-width: 768px) {
