    width: 80px;
    height: 80px;
    margin-bottom: 31px;
  }
  .about-program__icon--leaf {
    width: 26px;
  }
  .about-program__icon--tub {
    width: 30px;
  }
  .about-program__item--cutlery {
    width: 26px;
  }
  .about-program__icon--alarm {
    width: 25px;
  }
  .about-program__item {
    position: relative;
    padding-top: 0;
    padding-left: 0;
    margin-bottom: 0;
    min-height: 285px;
    align-items: flex-start;
    flex-direction: column;
  }
  .about-program__small {
    display: block;
    position: absolute;
    right: 77px;
    top: -41px;
