}
.exemple__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;
  margin: 0;
  margin-bottom: 34px;
}
.exemple__item-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid hsl(0, 0%, 80%);
  border-radius: 3px;
  min-height: 56px;
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
}
.exemple__item-description {
  font-size: 24px;
  line-height: 1;
