}
.exemple__item-definition {
  font-size: 12px;
  line-height: 1;
  font-family: "Arial", sans-serif;
  text-align: center;
  order: 1;
  position: absolute;
  right: 0;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  bottom: -15px;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
  background-color: hsl(0, 0%, 95%);
  width: min-content;
}
.exemple__list-spending {
  margin: 0;
  justify-content: center;
  display: flex;
  line-height: 20px;
  color: hsl(0, 0%, 0%);
}
.exemple__item-spending-definition {
  text-transform: uppercase;
}
.exemple__item-spending-description {
  margin: 0;
}
.exemple__range {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  margin-left: auto;
  margin-right: auto;
}
.exemple__range-container {
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
