    font-size: 16px;
    line-height: 24px;
    margin-bottom: 54px;
    width: 700px;
  }
  .exemple__list-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .exemple__item-container {
    min-width: 162px;
    min-height: 72px;
  }
  .exemple__item-definition {
    background-color: hsl(0, 0%, 100%);
    font-size: 14px;
    width: fit-content;
    bottom: -7px;
  }
  .exemple__item-description {
    font-size: 30px;
    line-height: 37px;
  }
  .exemple__list {
    margin: 0;
    column-gap: 42px;
  }
  .exemple__list-spending {
    font-size: 20px;
    flex-direction: column;
    align-items: flex-end;
  }
