  }
}
@media (min-width: 1440px) {
  .exemple {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 59px min-content 149px min-content 67px min-content 19px;
  }
  .exemple__title {
    grid-column: 1/2;
    grid-row: 2/3;
    margin: 0;
  }
  .exemple__content {
    width: 436px;
    grid-column: 1/2;
    grid-row: 4/5;
    margin: 0;
  }
  .exemple__list-wrapper {
    grid-column: 1/2;
    grid-row: 6/7;
    flex-direction: column;
    width: 436px;
    align-items: flex-start;
    margin: 0;
  }
  .exemple__item-definition {
    background-color: hsl(0, 0%, 95%);
  }
  .exemple__range-container {
    grid-column: 2/3;
    grid-row: 1/8;
    align-self: center;
  }
  .exemple__list-spending {
    flex-direction: row;
  }
  .exemple__item-spending-definition {
    margin: 0;
    margin-right: 59px;
  }
  .exemple__list {
    margin: 0;
    margin-bottom: 60px;
  }
}
